/* banner */
.homepage-banner {
  height: 420px;
  background-color: #1c036c;
}
.slick-slide img {
  margin: auto;
}
.homepage-banner .course-content {
  top: 0;
  width: inherit;
}
.course-sub-nav {
  box-sizing: border-box;
  width: 190px;
  height: 420px;
  font-size: 14px;
  background-color: rgba(0, 0, 0, 0.3);
}
.course-sub-nav li {
  padding: 0 20px;
  line-height: 44px;
}
.course-sub-nav li:nth-child(1) {
  padding-top: 13px;
}
.course-sub-nav a {
  color: #fff;
}
.course-sub-nav a::after {
  content: ">";
  position: absolute;
  right: 20px;
}
.course-sub-nav li:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.3);
}
.my-course-list {
  margin-top: 50px;
  height: 300px;
  background-color: #fff;
}
.my-course-list dt {
  width: 228px;
  height: 48px;
  line-height: 48px;
  font-size: 18px;
  font-weight: 600;
  background-color: #9bceea;
  color: #fff;
  text-align: center;
  margin-bottom: 20px;
}
.my-course-list dd {
  padding: 0 18px;
  color: #5a5a5a;
  height: 48px;
  border-bottom: 2px solid #efefef;
  margin-bottom: 15px;
}
.course-sub-nav li:hover a,
.my-course-list .active h4,
.my-course-list .active p,
.my-course-list dd:hover p {
  color: #00a4ff;
}
.my-course-list dd:last-child {
  margin-bottom: 10px;
  border: 0;
}
.my-course-list p {
  font-size: 12px;
  color: #ababab;
}
.my-course-list-button {
  width: 198px;
  height: 38px;
  margin-left: 18px;
}
/* banner */
/* suggestion card */
.modify-interested-field a {
  padding-right: 0;
  font-size: 14px;
  color: #00a4ff;
  border: 0;
}
/* suggestion card */

.homepage > .container {
  margin-top: -450px;
}

/* main */
.homepage-main {
  margin-top: 30px;
}
/* main */

.homepage-suggestion-list {
  margin-bottom: 38px;
}
/* subnav */
.sub-header {
  margin-bottom: 22px;
}
/* mid content */
.sub-header ul a {
  font-size: 14px;
  width: 32px;
  margin-right: 72px;
}
/* check more */
.sub-header a {
  font-size: 12px;
  color: #a5a5a5;
  margin: 0 30px 0 0;
}
.sub-header a:hover {
  color: #00a4ff;
  cursor: pointer;
}
.sub-header .selected {
  color: #00a4ff;
}
/* subnav */
/* main content */
.course-suggestion-top-banner {
  margin-bottom: 18px;
}
/* instructor */
.instructor {
  height: 320px;
  margin: 0 15px 15px 0;
  width: 228px;
}
.graduation-img {
  width: 486px;
  height: 320px;
}
.instructor-intro {
  box-sizing: border-box;
  height: 122px;
  bottom: 0;
  padding: 18px 28px 20px 25px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.4);
  transition: all 0.5s;
  overflow: hidden;
}
.instructor-intro:hover {
  height: 320px;
}
.instructor-intro:hover .instructor-desc {
  -webkit-line-clamp: 10;
}
.instructor-name {
  font-size: 18px;
  font-weight: 600;
}
.instructor-focus {
  margin-left: 12px;
}
.instructor-desc {
  margin-top: 14px;
  font-size: 13px;
  overflow: hidden;
  text-overflow: ellipsis;
  /* 兼容性问题 */
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.people-studied-instructor {
  top: 10px;
  right: -2px;
  box-sizing: border-box;
  height: 20px;
  font-size: 12px;
  color: #fb3f48;
}
.people-studied-triangle {
  border: 1px solid red;
  width: 2;
  width: 20px;
  background-color: white;
  z-index: 1;
  margin-right: -15px;
  transform: skew(-8deg, 0);
}
.people-studied-count {
  background-color: #fff;
  border: 1px solid red;
  border-left: 0;
  z-index: 2;
  padding-right: 2px;
}
/* instructor */
/* homepage scrollbar */
.homepage-scroll {
  position: sticky;
  margin-top: 20px;
  top: 100px;
  left: 175px;
  width: 185px;
  height: 430px;
  background-color: #fff;
  font-size: 14px;
}
.homepage-scroll ul {
  padding: 16px 26px;
}
.homepage-scroll li {
  height: 36px;
  line-height: 36px;
}
/* homepage scrollbar */
