.courselist-header {
  background-color: #fff;
  padding: 25px 0 10px;
  margin-bottom: 25px;
}
.courselist-header li {
  margin-top: 25px;
  padding-bottom: 19px;
  border-bottom: 1px solid #d7d7d7;
}
.courselist-header li:first-child {
  margin-top: 0;
}
.courselist-header li:last-child {
  border-bottom: 0;
}
.courselist-header dd,
.courselist-header dt {
  margin-right: 25px;
  font-size: 14px;
  padding: 1px 5px;
}
.courselist-header .selected,
.courselist-header .selected a {
  background-color: #00a4ff;
  color: #fff;
  text-align: center;
}
/* body-main */
.courselist-body-main {
  width: 972px;
}
.courselist-side {
  width: 228px;
  margin-bottom: 32px;
}
.courselist-side h4 {
  padding: 0 21px 18px;
  color: #00a4ff;
  font-weight: 600;
}
.courselist-side ul {
  background-color: #fff;
}
.courselist-side li {
  padding: 22px 24px 20px 20px;
  border-bottom: 1px solid #d7d7d7;
  background-color: #fff;
}
.courselist-side li:last-child {
  border-bottom: 0;
}
.courselist-top-banner {
  padding: 16px 22px 20px;
  color: #fff;
  line-height: 18px;
  font-size: 12px;
}
.courselist-course h4 {
  margin-bottom: 20px;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  color: #050505;
}
.courselist-course .course-info {
  margin: 0;
}
/* main body */
.courselist-main-header {
  margin-bottom: 20px;
}
.courselist-main-header li {
  margin: 0 25px 0 15px;
}
.courselist-main-header li a {
  color: #837e8a;
}
.courselist-main-header-pagination {
  margin-right: 25px;
}
.courselist-main-header li .selected {
  font-weight: 600;
}
.courselist-main-header-pagination,
.courselist-main-header-pagination a,
.courselist-main-header li .selected {
  color: #00a4ff;
}
.courselist-main-header-pagination .deactivate a,
.courselist-main-header-pagination .deactivate a:hover {
  color: #050505;
  cursor: default;
}
.pagination li {
  margin-right: 25px;
}
.pagination li:last-child {
  margin-right: 0;
}
.pagination-number {
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 42px;
}
.page-selected,
.page-selected a {
  background-color: #00a4ff;
  color: #fff;
}
/* main body */
